import React, { useEffect } from "react";
import { Slide } from "@mui/material";
import "./top.css";
import Confetti from "./confetti";
import { Image, Button, Row, Col, Container } from "react-bootstrap";
import image from "./assets/harsh.jpg";
import down from "./assets/down-arrow.svg";
// import image from "./assets/WhatsApp Image 2021-10-30 at 12.59.48 PM.jpeg";
import { width } from "@mui/system";

function Top() {
  const [loader, setLoader] = React.useState(false);


  useEffect(() => {
    for (var i = 0; i < 20; i++) {
      setTimeout(function () {
        setLoader(true);
      }, (i + i + 1) * 2000);
    }
  }, []);



  return (
    <div className="top">
      <Container fluid>
        <Row>
          <Col xs={12} md={8}>
            <div className="text">I'm Harsh Gupta</div>
            <Slide
              direction="up"
              in={true}
              mountOnEnter
              unmountOnExit
              style={{ transformOrigin: "0 0 0" }}
              {...(true ? { timeout: 2000 } : {})}
            >
              <div className="about">
                <p>
                  I am a Bachelor of Engineering Student in Information
                  Technology from MAIT Delhi.
                </p>
              </div>
            </Slide>
            {/* <Confetti /> */}
          </Col>
          <Col xs={6} md={4}>
            <Slide
              direction="down"
              in={true}
              mountOnEnter
              unmountOnExit
              style={{ transformOrigin: "0 0 0" }}
              {...(true ? { timeout: 2000 } : {})}
            >
              <Image src={image} className="image" roundedCircle />
            </Slide>
          </Col>
        </Row>
      </Container>
      {loader && <img className="down" src={down} />}
    </div>
  );
}

export default Top;
